import { Component, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

/**
 * @slot content - content of a single element
 * @slot button - button(s) of a single element
 */

@Component({
  tag: 'mdx-pd-item',
  styleUrl: 'pd-item.scss',
  shadow: true,
})
export class ProgressiveDisclosureItem {
  @Prop({ reflect: true })
  open = false;

  @Prop({ reflect: true })
  done = false;

  @Prop()
  step: string;

  @Prop()
  summary: string;

  @Prop()
  contentTitle: string;

  @Prop({ reflect: true })
  item!: string;

  render() {
    return (
      <details class="pd-item" open={this.open} aria-expanded={this.open}>
        <summary class="pd-item__summary-container">
          <div class="pd-item__summary">
            {this.done ? (
              <mdx-icon-check-circle class="pd-item__done-icon" size={ICON_SIZES.SM} />
            ) : (
              <div class="pd-item__todo-icon-container">
                <mdx-icon-circle class="pd-item__todo-icon" size={ICON_SIZES.SM} />
                <span class="pd-item__todo-step">{this.step}</span>
              </div>
            )}
            {this.summary}
          </div>
          {this.done && !this.open ? <mdx-icon-chevron-down class="pd-item__chevron" /> : null}
        </summary>
        <div class="pd-item__content-container">
          <div class="pd-item__content-title">{this.contentTitle}</div>
          <slot name="content" />
          <mdx-button-group>
            <slot name="button" />
          </mdx-button-group>
        </div>
      </details>
    );
  }
}
